.cell{
  position: relative;
  display: block;
  width: 100%;
  padding-top: 100%;
  overflow: hidden;
}
.cell .inner{
  position: absolute;
  display: block;
  width: 90%;
  height: 90%;
  top: calc(5% - 50em);
  left: calc(5% - 50em);
  border-radius: 50%;
  border: 50em solid #0394d8;
}


